
import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
  name: 'RButton',
})
export default class Index extends Vue {
  @Prop({ default: '', type: String })
  btnStyle!: string;

  render(): Vue.VNode {
    return (
      <button
        class={`r-button ${this.btnStyle}`.trim()}
        {...{ on: this.$listeners }}
        {...{ attrs: this.$attrs }}
        >{this.$slots.default || 'Button'}</button>
    );
  }
}
